/**
 * Settings Types
 * Defines all settings-related types and interfaces
 */

export type ThemeMode = 'light' | 'dark' | 'system'

export type Currency = 'USD' | 'EUR' | 'GBP' | 'JPY' | 'AUD' | 'CAD' | 'CHF' | 'CNY'

export type NumberFormat = '1,234.56' | '1 234.56' | '1.234,56' | '1234.56'

export type Timezone = 
  | 'UTC'
  | 'America/New_York'
  | 'America/Chicago'
  | 'America/Denver'
  | 'America/Los_Angeles'
  | 'Europe/London'
  | 'Europe/Paris'
  | 'Europe/Berlin'
  | 'Asia/Tokyo'
  | 'Asia/Shanghai'
  | 'Asia/Hong_Kong'

export type RiskTolerance = 'conservative' | 'moderate' | 'aggressive'

export type InvestmentGoal = 'growth' | 'income' | 'balanced' | 'preservation'

export type PreferredAssetClass = 'stocks' | 'etfs' | 'cryptocurrency' | 'forex' | 'commodities' | 'bonds'

export type NotificationType = 'price' | 'portfolio' | 'news' | 'digest'

export type NotificationChannel = 'push' | 'email' | 'sms'

export interface DisplaySettings {
  theme: ThemeMode
  currency: Currency
  numberFormat: NumberFormat
  compactMode: boolean
  showPriceChange: boolean
  animatedCharts: boolean
}

export interface NotificationSettings {
  enabled: boolean
  types: Record<NotificationType, boolean>
  channels: Record<NotificationChannel, boolean>
  quietHours: {
    enabled: boolean
    startTime: string
    endTime: string
  }
}

export interface ProfileSettings {
  firstName?: string
  lastName?: string
  email?: string
  phone?: string
  timezone: Timezone
  bio?: string
}

export interface InvestmentProfile {
  riskTolerance: RiskTolerance
  goal: InvestmentGoal
  preferredAssetClasses: PreferredAssetClass[]
}

export interface UserSettings {
  display: DisplaySettings
  notifications: NotificationSettings
  profile: ProfileSettings
  investment: InvestmentProfile
}

export interface SettingsUpdate {
  display?: Partial<DisplaySettings>
  notifications?: Partial<NotificationSettings>
  profile?: Partial<ProfileSettings>
  investment?: Partial<InvestmentProfile>
}

export const defaultDisplaySettings: DisplaySettings = {
  theme: 'system',
  currency: 'USD',
  numberFormat: '1,234.56',
  compactMode: false,
  showPriceChange: true,
  animatedCharts: true,
}

export const defaultNotificationSettings: NotificationSettings = {
  enabled: true,
  types: {
    price: true,
    portfolio: true,
    news: false,
    digest: false,
  },
  channels: {
    push: true,
    email: true,
    sms: false,
  },
  quietHours: {
    enabled: false,
    startTime: '22:00',
    endTime: '08:00',
  },
}

export const defaultProfileSettings: ProfileSettings = {
  timezone: 'America/New_York',
}

export const defaultInvestmentProfile: InvestmentProfile = {
  riskTolerance: 'moderate',
  goal: 'growth',
  preferredAssetClasses: ['stocks', 'etfs'],
}

export const defaultSettings: UserSettings = {
  display: defaultDisplaySettings,
  notifications: defaultNotificationSettings,
  profile: defaultProfileSettings,
  investment: defaultInvestmentProfile,
}

export interface ActiveSession {
  id: string
  device: string
  browser: string
  location: string
  lastActive: string
  current: boolean
}

export interface SecuritySettings {
  twoFactorEnabled: boolean
  twoFactorMethod: 'authenticator' | 'sms' | null
  sessions: ActiveSession[]
}
